import React from 'react'
import { Layout, Stack, Main, Sidebar, Hero } from '@layout'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import Categories from '@widgets/Categories'
import { useBlogCategoriesKanash } from '@helpers-blog'
import NbeikaPattern from '@components/NbeikaPattern'
import MemphisPattern from '@components/MemphisPattern'
import KsarPattern from '@components/KsarPattern'
import MapPattern from '@components/MapPattern'
import MoudjeriaPattern from '@components/MoudjeriaPattern'

const Posts = ({ ...props }) => {
  const { pageContext: { services = {} } = {} } = props
  const categories = useBlogCategoriesKanash()

  return (
    <Layout {...props}>
      <Seo title='أرشيف' />

      <Divider />

      <Hero wide>
        <Categories categories={categories} variant='horizontal' omitTitle />
      </Hero>
      <Hero>
        {/* <MoudjeriaPattern /> */}
        <KsarPattern />
        <MapPattern />
        {/* <MemphisPattern /> */}
        <NbeikaPattern />
        <Divider />
        <Divider space={5} />
        <Divider space={5} />
        <Divider space={5} />
        <Divider space={5} />
        <Divider space={5} />

        <Divider />
        <Divider />
        <Divider />
        <Divider />

        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
        <Divider />
      </Hero>
    </Layout>
  )
}

export default Posts
